#home {
  width: 100%;
  height: 70vh;
  margin-top: 190px;
  -webkit-justify-content: center;
          justify-content: center;
}
#home h1 {
  margin: 0 0 10px 0;
  font-size: 65px;
  font-weight: 700;
  color: black;
  font-family: "Open Sans", sans-serif;
}
#home .brand-name {
  color: #f85757;
  font-size: 60px;
}
#home h2 {
  color: #484848;
  font-size: 25px;
  font-family: "Open Sans", sans-serif;
}
#home .btn-get-started {
  font-size: 20px;
  font-weight: lighter;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 20px;
  border-radius: 50px;
  color: #f85757;
  border: 2px solid #f85757;
  text-decoration: none;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
}
#home .btn-get-started:hover {
  background-color: #f85757;
  color: white;
}
#home.header-img {
  text-align: right;
}

